* {
  color: white;
  font-family: sans-serif;
  text-align: center;
} 

html, body {
  height: 100%;
}

body {
  background-image: linear-gradient(30deg, black, rgb(31, 30, 30));
  min-height: 100%;
  display: flex;
  flex-direction: column;
  padding-top: 25%;
}
#location {
  font-size: 50px;
}

#weather-icon {
  width: 250px;
  margin: 0 auto;
  border: red solid;
}

#temperature {
  font-size: 25px;
  border: green dotted;
}

#condition {
  font-size: 20px;
  border: purple solid;
}
  
  
  